.calculator {
    background-color: #181818;
    color: $white;
}

.calculator__wrap {
    padding-bottom: 10px;
    padding-top: 100px;
    position: relative;
}

.calculator__title {
    font-size: px-to-rem(32px);
    line-height: px-to-rem(37px);
    color: $primary;
    text-align: center;
    margin: 0 0 20px 0;
    font-weight: 700;
}

.calculator__img {
    display: block;
    max-width: 100%;
    margin: 24px auto 32px;
    height: auto;
}

@media(min-width: 1024px) {
    .calculator__title {
        text-align: left;
        padding-left: 465px;
    }

    .calculator__img {
        position: absolute;
        left: 0;
        top: 50%;
        transform: translateY(calc(-50% + 20px));
    }

    .calculator__form {
        margin-left: 250px;
        padding: 5px 0 25px 215px;
    }
}